import { BrowserModule } from "@angular/platform-browser";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { NgModule } from "@angular/core";
import { HttpClientModule } from "@angular/common/http";
import { FormsModule } from "@angular/forms";
import { BsDropdownModule } from "ngx-bootstrap";
import { ModalModule } from "ngx-bootstrap";
import { AppRoutingModule } from "./app-routing.module";
import { NgxUiLoaderModule, NgxUiLoaderConfig } from "ngx-ui-loader";
import { AppComponent } from "./app.component";
import { CollapseModule } from 'ngx-bootstrap';
import { UserService } from "./components/admin/login/user-service";
import { AuthguardGuard } from "./components/admin/login/authguard.guard";
import { LoginguardGuard } from "./components/admin/login/loginGuard";
import { NavComponent } from './components/nav/nav.component';


const ngxUiLoaderConfig: NgxUiLoaderConfig = {
  bgsColor: "#fda300",
  bgsType: "cube-grid",
  blur: 15,
  fgsColor: "#fda300",
  fgsPosition: "center-center",
  fgsSize: 100,
  fgsType: "cube-grid",
  masterLoaderId: "master",
  overlayBorderRadius: "1",
  overlayColor: "rgba(0, 0, 0, 1)",
  pbColor: "#fda300",
  pbDirection: "ltr",
  pbThickness: 5,
  hasProgressBar: true,
  text: "Welcome",
  textColor: "#FFFFFF",
  textPosition: "center-center",
  maxTime: -1,
  minTime: 500
};

@NgModule({
  declarations: [
    AppComponent,
    NavComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    HttpClientModule,
    AppRoutingModule,
    ModalModule.forRoot(),
    BsDropdownModule.forRoot(),
    NgxUiLoaderModule.forRoot(ngxUiLoaderConfig),
    CollapseModule.forRoot(),

  ],
  providers: [UserService, AuthguardGuard, LoginguardGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }
